// export default Navbar;

"use client";

import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useRouter } from "next/navigation";
import Link from "next/link";
import axios from "axios";
import { LogOut, User2, Menu, X } from "lucide-react";
import { toast } from "sonner";
import { setUser } from "@/redux/slices/authSlice";
import { USER_API_ENDPOINT } from "@/lib/constant";

const Navbar = () => {
  const dispatch = useDispatch();
  const router = useRouter();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isPopoverOpen, setIsPopoverOpen] = useState(false);
  const { user } = useSelector((state) => state.auth);

  const logoutHandler = async () => {
    try {
      const res = await axios.get(`${USER_API_ENDPOINT}/logout`, {
        withCredentials: true,
      });
      if (res.data.success) {
        dispatch(setUser(null));
        router.push("/");
        toast.success(res.data.message);
      } else {
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error(error.response?.data?.message || "Logout failed");
    }
  };

  return (
    <nav className="sticky top-0 z-50 bg-white shadow-md">
      <div className="container mx-auto px-6 flex justify-between items-center h-16">
        {/* Logo */}
        <Link href="/" className="text-2xl font-bold text-gray-800">
          Job<span className="text-red-500">Portal</span>
        </Link>

        {/* Mobile Menu Button */}
        <button
          className="lg:hidden text-gray-800 focus:outline-none"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? (
            <X className="w-7 h-7" />
          ) : (
            <Menu className="w-7 h-7" />
          )}
        </button>

        {/* Navigation Links */}
        <ul
          className={`fixed inset-0 top-16 bg-white flex flex-col items-center gap-6 text-lg font-medium transform transition-transform duration-300 ease-in-out 
            ${isMenuOpen ? "translate-x-0" : "-translate-x-full"} 
            lg:static lg:flex lg:flex-row lg:gap-6 lg:translate-x-0 lg:bg-transparent`}
        >
          {user?.role === "recruiter" ? (
            <>
              <Link href="/recruiter/companies" className="hover:text-red-500">
                Companies
              </Link>
              <Link href="/recruiter/jobs" className="hover:text-red-500">
                Jobs
              </Link>
            </>
          ) : (
            <>
              <Link href="/" className="hover:text-red-500">
                Home
              </Link>
              <Link href="/jobs" className="hover:text-red-500">
                Jobs
              </Link>
              <Link href="/browse" className="hover:text-red-500">
                Browse
              </Link>
            </>
          )}

          {!user && (
            <div className="flex flex-col items-center gap-4 lg:hidden">
              <Link href="/login">
                <button className="w-40 border border-gray-300 py-2 rounded-md hover:bg-gray-100">
                  Login
                </button>
              </Link>
              <Link href="/signup">
                <button className="w-40 bg-red-500 text-white py-2 rounded-md hover:bg-red-600">
                  Signup
                </button>
              </Link>
            </div>
          )}
        </ul>

        {/* Auth Buttons / Avatar */}
        <div className="hidden lg:flex items-center gap-4">
          {!user ? (
            <>
              <Link href="/login">
                <button className="border border-gray-300 px-4 py-2 rounded-md hover:bg-gray-100">
                  Login
                </button>
              </Link>
              <Link href="/signup">
                <button className="bg-red-500 text-white px-4 py-2 rounded-md hover:bg-red-600">
                  Signup
                </button>
              </Link>
            </>
          ) : (
            <div className="relative">
              <button
                onClick={() => setIsPopoverOpen(!isPopoverOpen)}
                className="relative w-10 h-10 rounded-full overflow-hidden border-2 border-gray-300"
              >
                <img
                  src={user?.profile?.profileImage || "/default-avatar.png"}
                  alt="User Avatar"
                  className="w-full h-full object-cover"
                />
              </button>

              {/* Popover */}
              {isPopoverOpen && (
                <div
                  className="absolute right-0 mt-2 w-60 bg-white shadow-lg rounded-lg p-4"
                  onClick={() => setIsPopoverOpen(false)}
                >
                  <div className="flex items-center gap-4">
                    <img
                      src={user?.profile?.profileImage || "/default-avatar.png"}
                      className="w-12 h-12 rounded-full"
                      alt="Profile"
                    />
                    <div>
                      <h4 className="font-medium">{user?.fullname}</h4>
                      <p className="text-sm text-gray-500">{user?.role}</p>
                    </div>
                  </div>

                  <div className="mt-4">
                    <Link
                      href="/profile"
                      className="flex items-center gap-2 py-2 hover:text-red-500"
                    >
                      <User2 className="w-5 h-5" /> Profile
                    </Link>
                    <button
                      onClick={logoutHandler}
                      className="flex items-center gap-2 py-2 w-full text-left hover:text-red-500"
                    >
                      <LogOut className="w-5 h-5" /> Logout
                    </button>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
